import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '../firebase';
import { UserWithSubscription, SubscriptionPlan, isSubscriptionValid } from './subscriptionService';

//  
export enum NotificationType {
  CRITICAL = 'critical',    //    
  WARNING = 'warning',      // 3   
  INFO = 'info',           // 7   
  EXPIRED = 'expired'      //  
}

//  
export interface SubscriptionNotification {
  id: string;
  userId: string;
  userName: string;
  userEmail: string;
  subscriptionPlan: SubscriptionPlan;
  expiryDate: Date;
  daysRemaining: number;
  type: NotificationType;
  message: string;
  isRead: boolean;
  createdAt: Date;
}

//  
export interface NotificationSettings {
  enableVisualAlerts: boolean;
  enableAudioAlerts: boolean;
  criticalDays: number;    //    (: 1)
  warningDays: number;     //    (: 3)
  infoDays: number;        //    (: 7)
}

//  
export const defaultNotificationSettings: NotificationSettings = {
  enableVisualAlerts: true,
  enableAudioAlerts: true,
  criticalDays: 1,
  warningDays: 3,
  infoDays: 7
};

//   
export const calculateDaysRemaining = (expiryDate: Date): number => {
  const today = new Date();
  const expiry = new Date(expiryDate);
  const diffTime = expiry.getTime() - today.getTime();
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  return diffDays;
};

//       
export const getNotificationType = (
  daysRemaining: number, 
  settings: NotificationSettings = defaultNotificationSettings
): NotificationType => {
  if (daysRemaining < 0) {
    return NotificationType.EXPIRED;
  } else if (daysRemaining <= settings.criticalDays) {
    return NotificationType.CRITICAL;
  } else if (daysRemaining <= settings.warningDays) {
    return NotificationType.WARNING;
  } else if (daysRemaining <= settings.infoDays) {
    return NotificationType.INFO;
  }
  return NotificationType.INFO;
};

//   
export const createNotificationMessage = (
  userName: string,
  daysRemaining: number,
  plan: SubscriptionPlan
): string => {
  const planName = getPlanDisplayName(plan);
  
  if (daysRemaining < 0) {
    return `   ${planName}  ${userName}`;
  } else if (daysRemaining === 0) {
    return `  ${planName}  ${userName} !`;
  } else if (daysRemaining === 1) {
    return `  ${planName}  ${userName} `;
  } else {
    return `  ${planName}  ${userName}  ${daysRemaining} `;
  }
};

//     
const getPlanDisplayName = (plan: SubscriptionPlan): string => {
  switch (plan) {
    case SubscriptionPlan.MONTHLY:
      return '';
    case SubscriptionPlan.YEARLY:
      return '';
    case SubscriptionPlan.LIFETIME:
      return ' ';
    default:
      return ' ';
  }
};

//     
export const getActiveSubscriptions = async (): Promise<UserWithSubscription[]> => {
  try {
    const usersRef = collection(db, 'users');
    const q = query(usersRef, where('subscription', '!=', null));
    const querySnapshot = await getDocs(q);
    
    const users: UserWithSubscription[] = [];
    querySnapshot.forEach((doc) => {
      const userData = doc.data();
      if (userData.subscription && (userData.subscription.planType || userData.subscription.plan)) {
        users.push({
          id: doc.id,
          ...userData,
          subscription: {
            ...userData.subscription,
            //    Timestamp  Date  string
            startDate: (userData.subscription.startDate as any)?.toDate?.() || new Date(userData.subscription.startDate),
            endDate: (userData.subscription.endDate as any)?.toDate?.() || new Date(userData.subscription.endDate),
            //      
            planType: userData.subscription.planType || userData.subscription.plan
          }
        } as UserWithSubscription);
      }
    });
    
    return users;
  } catch (error) {
    console.error('    :', error);
    return [];
  }
};

//    
export const generateSubscriptionNotifications = async (
  settings: NotificationSettings = defaultNotificationSettings
): Promise<SubscriptionNotification[]> => {
  try {
    const users = await getActiveSubscriptions();
    const notifications: SubscriptionNotification[] = [];
    
    users.forEach((user) => {
      if (!user.subscription || !user.subscription.endDate) return;
      
      const daysRemaining = calculateDaysRemaining(user.subscription.endDate);
      const notificationType = getNotificationType(daysRemaining, settings);
      
      //           
      if (daysRemaining <= settings.infoDays || daysRemaining < 0) {
        const notification: SubscriptionNotification = {
          id: `${user.id}_${Date.now()}`,
          userId: user.id,
          userName: user.displayName || user.email || '  ',
          userEmail: user.email || '',
          subscriptionPlan: user.subscription.planType,
          expiryDate: user.subscription.endDate,
          daysRemaining,
          type: notificationType,
          message: createNotificationMessage(
            user.displayName || user.email || '  ',
            daysRemaining,
            user.subscription.planType
          ),
          isRead: false,
          createdAt: new Date()
        };
        
        notifications.push(notification);
      }
    });
    
    //     (  )
    notifications.sort((a, b) => {
      const priorityOrder = {
        [NotificationType.EXPIRED]: 0,
        [NotificationType.CRITICAL]: 1,
        [NotificationType.WARNING]: 2,
        [NotificationType.INFO]: 3
      };
      
      if (priorityOrder[a.type] !== priorityOrder[b.type]) {
        return priorityOrder[a.type] - priorityOrder[b.type];
      }
      
      return a.daysRemaining - b.daysRemaining;
    });
    
    return notifications;
  } catch (error) {
    console.error('    :', error);
    return [];
  }
};

//    
export const filterNotificationsByType = (
  notifications: SubscriptionNotification[],
  type?: NotificationType
): SubscriptionNotification[] => {
  if (!type) return notifications;
  return notifications.filter(notification => notification.type === type);
};

//     
export const filterNotificationsByDays = (
  notifications: SubscriptionNotification[],
  maxDays?: number
): SubscriptionNotification[] => {
  if (maxDays === undefined) return notifications;
  return notifications.filter(notification => notification.daysRemaining <= maxDays);
};

//      
export const getUnreadNotificationsCount = (notifications: SubscriptionNotification[]): number => {
  return notifications.filter(notification => !notification.isRead).length;
};

//    
export const getCriticalNotifications = (notifications: SubscriptionNotification[]): SubscriptionNotification[] => {
  return notifications.filter(notification => 
    notification.type === NotificationType.CRITICAL || 
    notification.type === NotificationType.EXPIRED
  );
};

//     localStorage
export const saveNotificationSettings = (settings: NotificationSettings): void => {
  localStorage.setItem('notificationSettings', JSON.stringify(settings));
};

//     localStorage
export const loadNotificationSettings = (): NotificationSettings => {
  try {
    const saved = localStorage.getItem('notificationSettings');
    if (saved) {
      return { ...defaultNotificationSettings, ...JSON.parse(saved) };
    }
  } catch (error) {
    console.error('    :', error);
  }
  return defaultNotificationSettings;
};

//     (     )
export const subscribeToSubscriptionChanges = (
  callback: (notifications: SubscriptionNotification[]) => void,
  settings: NotificationSettings = defaultNotificationSettings
) => {
  //      
  //      
  generateSubscriptionNotifications(settings).then(callback);
  return () => {};
};
