import React, { useState, useEffect } from 'react';
import { Bell, Filter, X, AlertTriangle, Clock, User, Calendar } from 'lucide-react';
import { 
  generateSubscriptionNotifications, 
  filterNotificationsByType, 
  filterNotificationsByDays,
  SubscriptionNotification, 
  NotificationType,
  loadNotificationSettings,
  saveNotificationSettings,
  NotificationSettings,
  defaultNotificationSettings
} from '../utils/notificationService';

interface NotificationCenterProps {
  isOpen: boolean;
  onClose: () => void;
}

const NotificationCenter: React.FC<NotificationCenterProps> = ({ isOpen, onClose }) => {
  const [notifications, setNotifications] = useState<SubscriptionNotification[]>([]);
  const [filteredNotifications, setFilteredNotifications] = useState<SubscriptionNotification[]>([]);
  const [filterType, setFilterType] = useState<'all' | NotificationType>('all');
  const [loading, setLoading] = useState(true);
  const [settings, setSettings] = useState(loadNotificationSettings());

  useEffect(() => {
    if (isOpen) {
      loadNotifications();
    }
  }, [isOpen]);

  useEffect(() => {
    if (filterType === 'all') {
      setFilteredNotifications(notifications);
    } else {
      setFilteredNotifications(filterNotificationsByType(notifications, filterType));
    }
  }, [notifications, filterType]);

  const loadNotifications = async () => {
    setLoading(true);
    try {
      const notifs = await generateSubscriptionNotifications(settings);
      setNotifications(notifs);
    } catch (error) {
      console.error('   :', error);
    } finally {
      setLoading(false);
    }
  };

  const getNotificationIcon = (type: NotificationType) => {
    switch (type) {
      case 'urgent':
        return <AlertTriangle className="w-5 h-5 text-red-500" />;
      case 'warning':
        return <Clock className="w-5 h-5 text-yellow-500" />;
      case 'info':
        return <Bell className="w-5 h-5 text-blue-500" />;
      default:
        return <Bell className="w-5 h-5 text-gray-500" />;
    }
  };

  const getNotificationBgColor = (type: NotificationType) => {
    switch (type) {
      case 'urgent':
        return 'bg-red-50 border-red-200';
      case 'warning':
        return 'bg-yellow-50 border-yellow-200';
      case 'info':
        return 'bg-blue-50 border-blue-200';
      default:
        return 'bg-gray-50 border-gray-200';
    }
  };

  const formatDate = (date: Date) => {
    return new Intl.DateTimeFormat('ar-SA', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };

  const getFilterLabel = (type: 'all' | NotificationType) => {
    switch (type) {
      case 'all':
        return ' ';
      case 'urgent':
        return ' ( )';
      case 'warning':
        return ' (3 )';
      case 'info':
        return ' (7 )';
      default:
        return ' ';
    }
  };

  const toggleSetting = (key: keyof typeof settings) => {
    const newSettings = { ...settings, [key]: !settings[key] };
    setSettings(newSettings);
    saveNotificationSettings(newSettings);
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center">
      <div className="bg-white rounded-lg shadow-xl w-full max-w-4xl max-h-[90vh] overflow-hidden">
        {/* Header */}
        <div className="bg-gradient-to-r from-blue-600 to-purple-600 text-white p-6">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <Bell className="w-6 h-6" />
              <h2 className="text-xl font-bold"> </h2>
              {notifications.length > 0 && (
                <span className="bg-white text-blue-600 px-2 py-1 rounded-full text-sm font-medium">
                  {notifications.length}
                </span>
              )}
            </div>
            <button
              onClick={onClose}
              className="p-2 hover:bg-white hover:bg-opacity-20 rounded-lg transition-colors"
            >
              <X className="w-5 h-5" />
            </button>
          </div>
        </div>

        {/* Filters and Settings */}
        <div className="p-4 border-b bg-gray-50">
          <div className="flex flex-wrap items-center gap-4 mb-4">
            <div className="flex items-center gap-2">
              <Filter className="w-4 h-4 text-gray-600" />
              <span className="text-sm font-medium text-gray-700"> :</span>
            </div>
            {(['all', 'urgent', 'warning', 'info'] as const).map((type) => (
              <button
                key={type}
                onClick={() => setFilterType(type)}
                className={`px-3 py-1 rounded-full text-sm font-medium transition-colors ${
                  filterType === type
                    ? 'bg-blue-600 text-white'
                    : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                }`}
              >
                {getFilterLabel(type)}
              </button>
            ))}
          </div>

          {/* Settings */}
          <div className="flex flex-wrap items-center gap-4">
            <span className="text-sm font-medium text-gray-700">:</span>
            <label className="flex items-center gap-2 cursor-pointer">
              <input
                type="checkbox"
                checked={settings.visualAlerts}
                onChange={() => toggleSetting('visualAlerts')}
                className="rounded"
              />
              <span className="text-sm"> </span>
            </label>
            <label className="flex items-center gap-2 cursor-pointer">
              <input
                type="checkbox"
                checked={settings.soundAlerts}
                onChange={() => toggleSetting('soundAlerts')}
                className="rounded"
              />
              <span className="text-sm"> </span>
            </label>
            <label className="flex items-center gap-2 cursor-pointer">
              <input
                type="checkbox"
                checked={settings.autoRefresh}
                onChange={() => toggleSetting('autoRefresh')}
                className="rounded"
              />
              <span className="text-sm"> </span>
            </label>
          </div>
        </div>

        {/* Notifications List */}
        <div className="flex-1 overflow-y-auto max-h-96">
          {loading ? (
            <div className="flex items-center justify-center p-8">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
              <span className="mr-3 text-gray-600">  ...</span>
            </div>
          ) : filteredNotifications.length === 0 ? (
            <div className="text-center p-8">
              <Bell className="w-16 h-16 text-gray-300 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">  </h3>
              <p className="text-gray-500">
                {filterType === 'all' 
                  ? '   '
                  : `     "${getFilterLabel(filterType)}"`
                }
              </p>
            </div>
          ) : (
            <div className="p-4 space-y-3">
              {filteredNotifications.map((notification) => (
                <div
                  key={notification.id}
                  className={`p-4 rounded-lg border-r-4 ${getNotificationBgColor(notification.type)}`}
                >
                  <div className="flex items-start gap-3">
                    {getNotificationIcon(notification.type)}
                    <div className="flex-1">
                      <div className="flex items-center justify-between mb-2">
                        <h4 className="font-medium text-gray-900">
                          {notification.message}
                        </h4>
                        <span className="text-xs text-gray-500">
                          {formatDate(notification.createdAt)}
                        </span>
                      </div>
                      
                      <div className="grid grid-cols-1 md:grid-cols-3 gap-3 text-sm">
                        <div className="flex items-center gap-2">
                          <User className="w-4 h-4 text-gray-400" />
                          <span className="text-gray-700">
                            <strong>:</strong> {notification.userName}
                          </span>
                        </div>
                        
                        <div className="flex items-center gap-2">
                          <Calendar className="w-4 h-4 text-gray-400" />
                          <span className="text-gray-700">
                            <strong> :</strong> {formatDate(notification.expiryDate)}
                          </span>
                        </div>
                        
                        <div className="flex items-center gap-2">
                          <Clock className="w-4 h-4 text-gray-400" />
                          <span className="text-gray-700">
                            <strong> :</strong> {notification.daysRemaining}
                          </span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Footer */}
        <div className="p-4 border-t bg-gray-50">
          <div className="flex items-center justify-between">
            <div className="text-sm text-gray-600">
               : {formatDate(new Date())}
            </div>
            <button
              onClick={loadNotifications}
              className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors text-sm"
            >
               
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default NotificationCenter;