import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Switch } from '@/components/ui/switch';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { 
  MessageCircle, 
  Phone, 
  Save, 
  RefreshCw, 
  CheckCircle, 
  AlertCircle,
  ExternalLink,
  Copy,
  Eye,
  EyeOff
} from 'lucide-react';
import { 
  WhatsAppSettings,
  useWhatsAppSettings,
  validatePhoneNumber,
  createWhatsAppLink,
  formatPhoneNumber
} from '@/utils/whatsappService';
import { useToast } from '@/hooks/use-toast';

const WhatsAppManagement: React.FC = () => {
  const { settings, loading, error, updateSettings, refreshSettings } = useWhatsAppSettings();
  const { toast } = useToast();
  
  //  
  const [formData, setFormData] = useState<Partial<WhatsAppSettings>>({
    phoneNumber: '',
    defaultMessage: '',
    isEnabled: true
  });
  
  //   
  const [isSaving, setIsSaving] = useState(false);
  const [showPreview, setShowPreview] = useState(false);
  const [previewLink, setPreviewLink] = useState('');
  const [validationErrors, setValidationErrors] = useState<Record<string, string>>({});

  //     
  useEffect(() => {
    if (settings) {
      setFormData({
        phoneNumber: settings.phoneNumber,
        defaultMessage: settings.defaultMessage,
        isEnabled: settings.isEnabled
      });
    }
  }, [settings]);

  //    
  const validateForm = (): boolean => {
    const errors: Record<string, string> = {};
    
    if (!formData.phoneNumber?.trim()) {
      errors.phoneNumber = '  ';
    } else if (!validatePhoneNumber(formData.phoneNumber)) {
      errors.phoneNumber = '    (    10-15 )';
    }
    
    if (!formData.defaultMessage?.trim()) {
      errors.defaultMessage = '  ';
    } else if (formData.defaultMessage.length < 10) {
      errors.defaultMessage = '     10   ';
    }
    
    setValidationErrors(errors);
    return Object.keys(errors).length === 0;
  };

  //  
  const handleSave = async () => {
    if (!validateForm()) {
      toast({
        title: "  ",
        description: "    ",
        variant: "destructive"
      });
      return;
    }

    setIsSaving(true);
    try {
      const success = await updateSettings(formData);
      if (success) {
        toast({
          title: "  ",
          description: "    ",
          variant: "default"
        });
      } else {
        throw new Error('  ');
      }
    } catch (error) {
      toast({
        title: "  ",
        description: "    ",
        variant: "destructive"
      });
    } finally {
      setIsSaving(false);
    }
  };

  //  
  const handlePreview = () => {
    if (formData.phoneNumber && formData.defaultMessage) {
      try {
        const link = createWhatsAppLink(formData.phoneNumber, formData.defaultMessage);
        setPreviewLink(link);
        setShowPreview(true);
      } catch (error) {
        toast({
          title: "  ",
          description: "    ",
          variant: "destructive"
        });
      }
    }
  };

  //  
  const copyLink = async () => {
    try {
      await navigator.clipboard.writeText(previewLink);
      toast({
        title: " ",
        description: "    ",
        variant: "default"
      });
    } catch (error) {
      toast({
        title: "  ",
        description: "   ",
        variant: "destructive"
      });
    }
  };

  //     
  const handlePhoneChange = (value: string) => {
    //     
    const cleaned = value.replace(/[^\d+]/g, '');
    setFormData(prev => ({ ...prev, phoneNumber: cleaned }));
    
    //     
    if (validationErrors.phoneNumber) {
      setValidationErrors(prev => ({ ...prev, phoneNumber: '' }));
    }
  };

  if (loading) {
    return (
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <MessageCircle className="h-5 w-5" />
             
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-center py-8">
            <RefreshCw className="h-6 w-6 animate-spin" />
            <span className="mr-2"> ...</span>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <MessageCircle className="h-5 w-5" />
             
          </CardTitle>
          <CardDescription>
                     
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {error && (
            <Alert variant="destructive">
              <AlertCircle className="h-4 w-4" />
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {/*   */}
          <div className="flex items-center justify-between p-4 bg-muted rounded-lg">
            <div className="flex items-center gap-3">
              <div className={`h-3 w-3 rounded-full ${formData.isEnabled ? 'bg-green-500' : 'bg-red-500'}`} />
              <div>
                <p className="font-medium">  </p>
                <p className="text-sm text-muted-foreground">
                  {formData.isEnabled ? '' : ''}
                </p>
              </div>
            </div>
            <Switch
              checked={formData.isEnabled}
              onCheckedChange={(checked) => 
                setFormData(prev => ({ ...prev, isEnabled: checked }))
              }
            />
          </div>

          <Separator />

          {/*   */}
          <div className="space-y-2">
            <Label htmlFor="phoneNumber" className="flex items-center gap-2">
              <Phone className="h-4 w-4" />
               
            </Label>
            <Input
              id="phoneNumber"
              type="tel"
              placeholder=": 201000392539"
              value={formData.phoneNumber}
              onChange={(e) => handlePhoneChange(e.target.value)}
              className={validationErrors.phoneNumber ? 'border-red-500' : ''}
              dir="ltr"
            />
            {validationErrors.phoneNumber && (
              <p className="text-sm text-red-500">{validationErrors.phoneNumber}</p>
            )}
            <p className="text-sm text-muted-foreground">
                    (: 201000392539 )
            </p>
          </div>

          {/*   */}
          <div className="space-y-2">
            <Label htmlFor="defaultMessage"> </Label>
            <Textarea
              id="defaultMessage"
              placeholder="    "
              value={formData.defaultMessage}
              onChange={(e) => {
                setFormData(prev => ({ ...prev, defaultMessage: e.target.value }));
                if (validationErrors.defaultMessage) {
                  setValidationErrors(prev => ({ ...prev, defaultMessage: '' }));
                }
              }}
              className={validationErrors.defaultMessage ? 'border-red-500' : ''}
              rows={4}
            />
            {validationErrors.defaultMessage && (
              <p className="text-sm text-red-500">{validationErrors.defaultMessage}</p>
            )}
            <p className="text-sm text-muted-foreground">
                      
            </p>
          </div>

          {/*   */}
          <div className="flex gap-3 pt-4">
            <Button 
              onClick={handleSave} 
              disabled={isSaving}
              className="flex items-center gap-2"
            >
              {isSaving ? (
                <RefreshCw className="h-4 w-4 animate-spin" />
              ) : (
                <Save className="h-4 w-4" />
              )}
              {isSaving ? ' ...' : ' '}
            </Button>
            
            <Button 
              variant="outline" 
              onClick={handlePreview}
              disabled={!formData.phoneNumber || !formData.defaultMessage}
              className="flex items-center gap-2"
            >
              {showPreview ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
               
            </Button>
            
            <Button 
              variant="outline" 
              onClick={refreshSettings}
              className="flex items-center gap-2"
            >
              <RefreshCw className="h-4 w-4" />
              
            </Button>
          </div>

          {/*   */}
          {showPreview && previewLink && (
            <Card className="bg-muted/50">
              <CardHeader>
                <CardTitle className="text-lg">  </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="p-3 bg-background rounded border font-mono text-sm break-all">
                  {previewLink}
                </div>
                <div className="flex gap-2">
                  <Button 
                    variant="outline" 
                    size="sm"
                    onClick={copyLink}
                    className="flex items-center gap-2"
                  >
                    <Copy className="h-4 w-4" />
                     
                  </Button>
                  <Button 
                    variant="outline" 
                    size="sm"
                    onClick={() => window.open(previewLink, '_blank')}
                    className="flex items-center gap-2"
                  >
                    <ExternalLink className="h-4 w-4" />
                      
                  </Button>
                </div>
              </CardContent>
            </Card>
          )}

          {/*   */}
          {settings && (
            <div className="pt-4 border-t">
              <h4 className="font-medium mb-3"> </h4>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                <div>
                  <span className="text-muted-foreground"> :</span>
                  <Badge variant="outline" className="mr-2">
                    {settings.lastUpdated.toLocaleDateString('ar-EG')} - {settings.lastUpdated.toLocaleTimeString('ar-EG')}
                  </Badge>
                </div>
                <div>
                  <span className="text-muted-foreground">:</span>
                  <Badge variant={settings.isEnabled ? "default" : "secondary"} className="mr-2">
                    {settings.isEnabled ? '' : ''}
                  </Badge>
                </div>
              </div>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
};

export default WhatsAppManagement;