import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import AdminDashboard from '../pages/AdminDashboard';
import { auth } from '../firebase';
import { collection, query, getDocs } from 'firebase/firestore';
import { db } from '../firebase';
import { loadContentFromFirebase as loadPlans, saveContentToFirebase as savePlans, getLastUpdateInfo } from '@/utils/contentService';
import LogoLoader from '@/components/LogoLoader';

const ProtectedAdminDashboard = () => {
  const navigate = useNavigate();
  const { user, loading: authLoading } = useFirebaseAuth();
  const [devMode, setDevMode] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const mergeUsers = (rUsers: any[], pMap: Record<string, any>) => {
    return rUsers.map((u) => {
      const p = pMap[u.id] || {};
      const fullName = typeof p.fullName === 'string' ? p.fullName : undefined;
      const phone = typeof p.phone === 'string' ? p.phone : undefined;
      const displayName = (typeof u.displayName === 'string' && u.displayName.trim())
        ? u.displayName
        : (fullName || u.displayName || undefined);
      return {
        ...u,
        displayName,
        phone: u.phone ?? phone ?? null,
      };
    });
  };

  const [loading, setLoading] = useState(true);
  
  const [activeTab, setActiveTab] = useState('users');
  const [notificationCount, setNotificationCount] = useState(0);
  
  const [rawUsers, setRawUsers] = useState<any[]>([]);
  const [profilesMap, setProfilesMap] = useState<Record<string, any>>({});
  
  const [users, setUsers] = useState<any[]>([]);
  const [filteredUsers, setFilteredUsers] = useState<any[]>([]);
  
  const [isSavingContent, setIsSavingContent] = useState(false);
  const [lastUpdateInfo, setLastUpdateInfo] = useState<any>(null);

  // Check for dev mode on component mount
  useEffect(() => {
    const devModeEnabled = localStorage.getItem('admin_dev_mode') === 'true';
    setDevMode(devModeEnabled);
  }, []);

  //     
  const refreshData = async () => {
    if (!devMode && !user) return;
    
    setLoading(true);
    setError(null);
    try {
      //  
      const usersQuery = query(collection(db, 'users'));
      const usersSnap = await import('firebase/firestore').then(m => m.getDocs(usersQuery));
      const fullUsers = usersSnap.docs.map((d) => ({ id: d.id, ...d.data() }));
      setRawUsers(fullUsers);

      //  
      const profilesQuery = query(collection(db, 'profiles'));
      const profilesSnap = await import('firebase/firestore').then(m => m.getDocs(profilesQuery));
      const fullProfiles: Record<string, any> = {};
      profilesSnap.forEach((doc) => {
        fullProfiles[doc.id] = doc.data();
      });
      setProfilesMap(fullProfiles);
      
      setLoading(false);
    } catch (error) {
      console.error('Error refreshing data:', error);
      if (!devMode && rawUsers.length === 0) {
        setError('   ');
      }
      setLoading(false);
    }
  };

  //    
  useEffect(() => {
    refreshData();
  }, [user, devMode]);

  // New: Merge raw users with profilesMap to fill displayName and phone
  useEffect(() => {
    // console.log('ProtectedAdminDashboard: Merging users. Raw count:', rawUsers.length);
    const merged = rawUsers.map((u) => {
      const p = profilesMap[u.id] || {};
      const fullName = typeof p.fullName === 'string' ? p.fullName : undefined;
      const phone = typeof p.phone === 'string' ? p.phone : undefined;
      const displayName = (typeof u.displayName === 'string' && u.displayName.trim())
        ? u.displayName
        : (fullName || u.displayName || undefined);
      return {
        ...u,
        displayName,
        phone: u.phone ?? phone ?? null,
      };
    });
    
    // Always update users and filteredUsers to ensure they are in sync
    // This fixes the issue where users might disappear if filteredUsers gets out of sync
    setUsers(merged);
  }, [rawUsers, profilesMap]);

  // Dummy functions for props that AdminDashboard expects
  const setNotificationCenterOpen = (open: boolean) => {
    console.log('Notification center:', open);
  };

  const setNotificationSettings = (settings: any) => {
    console.log('Notification settings:', settings);
  };

  const setNotifications = (notifications: any[]) => {
    console.log('Notifications:', notifications);
  };

  const setEditingPlan = (plan: any) => {
    console.log('Editing plan:', plan);
  };

  useEffect(() => {
    //        (  )
    if (!user) return;
    (async () => {
      try {
        const info = await getLastUpdateInfo();
        setLastUpdateInfo(info);
      } catch (e) {
        console.warn('Failed to load last update info:', e);
        setLastUpdateInfo(null);
      }
    })();
  }, [user]);

  const loadContentFromFirebase = async () => {
    try {
      const data = await loadPlans();
      return data;
    } catch (e) {
      console.error('Error loading content:', e);
      return {};
    }
  };

  const saveContentToFirebase = async (content: any) => {
    try {
      setIsSavingContent(true);
      await savePlans(content, user?.uid || 'admin');
      const info = await getLastUpdateInfo();
      setLastUpdateInfo(info);
      return true;
    } catch (e) {
      console.error('Error saving content:', e);
      return false;
    } finally {
      setIsSavingContent(false);
    }
  };

  if (authLoading) {
    return <LogoLoader />;
  }

  return (
    <AdminDashboard
      navigate={navigate}
      auth={auth}
      devMode={devMode}
      setDevMode={setDevMode}
      error={error}
      setError={setError}
      authLoading={authLoading}
      setAuthLoading={() => {}} // Firebase auth handles this
      activeTab={activeTab}
      setActiveTab={setActiveTab}
      setNotificationCenterOpen={setNotificationCenterOpen}
      setNotificationSettings={setNotificationSettings}
      setNotifications={setNotifications}
      notificationCount={notificationCount}
      setNotificationCount={setNotificationCount}
      users={users}
      setUsers={setUsers}
      loading={loading}
      setLoading={setLoading}
      setEditingPlan={setEditingPlan}
      isSavingContent={isSavingContent}
      setIsSavingContent={setIsSavingContent}
      lastUpdateInfo={lastUpdateInfo}
      loadContentFromFirebase={loadContentFromFirebase}
      saveContentToFirebase={saveContentToFirebase}
      refreshData={refreshData}
    />
  );
};

export default ProtectedAdminDashboard;
