import { db } from '@/firebase';
import { collection, addDoc, getDocs, query, where, updateDoc, doc, serverTimestamp, getDoc } from 'firebase/firestore';

export interface BranchActivationCode {
  id?: string;
  code: string;
  isUsed: boolean;
  createdAt: any;
  createdBy: string;
  usedAt?: any;
  usedBy?: string; // userId of the merchant who used it
  createdShopId?: string; // ID of the shop created with this code
}

export const branchActivationService = {
  /**
   * Generates a new unique activation code
   */
  generateCode: async (adminId: string): Promise<string> => {
    // Generate a random 6-character alphanumeric code (uppercase)
    const chars = 'ABCDEFGHJKLMNPQRSTUVWXYZ23456789'; // Removed confusing chars like I, 1, O, 0
    let code = '';
    for (let i = 0; i < 8; i++) {
      code += chars.charAt(Math.floor(Math.random() * chars.length));
    }
    
    // Add hyphen for readability: XXXX-XXXX
    const formattedCode = `${code.substring(0, 4)}-${code.substring(4)}`;

    await addDoc(collection(db, 'branchActivationCodes'), {
      code: formattedCode,
      isUsed: false,
      createdAt: serverTimestamp(),
      createdBy: adminId
    });

    return formattedCode;
  },

  /**
   * Gets all activation codes (for admin view)
   */
  getAllCodes: async (): Promise<BranchActivationCode[]> => {
    const q = query(collection(db, 'branchActivationCodes'));
    const snapshot = await getDocs(q);
    return snapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data()
    } as BranchActivationCode)).sort((a, b) => {
        // Sort by createdAt desc
        const tA = a.createdAt?.toMillis ? a.createdAt.toMillis() : 0;
        const tB = b.createdAt?.toMillis ? b.createdAt.toMillis() : 0;
        return tB - tA;
    });
  },

  /**
   * Validates a code
   * Returns the code document data if valid, throws error otherwise
   */
  validateCode: async (code: string): Promise<BranchActivationCode & { docId: string }> => {
    const q = query(collection(db, 'branchActivationCodes'), where('code', '==', code));
    const snapshot = await getDocs(q);

    if (snapshot.empty) {
      throw new Error('   ');
    }

    const docData = snapshot.docs[0].data() as BranchActivationCode;
    const docId = snapshot.docs[0].id;

    if (docData.isUsed) {
      throw new Error('    ');
    }

    return { ...docData, docId };
  },

  /**
   * Marks a code as used
   */
  markCodeAsUsed: async (docId: string, userId: string, shopId: string) => {
    const ref = doc(db, 'branchActivationCodes', docId);
    await updateDoc(ref, {
      isUsed: true,
      usedAt: serverTimestamp(),
      usedBy: userId,
      createdShopId: shopId
    });
  }
};
