import React, { useState, useMemo } from 'react';
import { Zap, Calendar, Gift, Crown, Clock, Lock, RefreshCw, X, CheckCircle } from 'lucide-react';
import { SubscriptionPlan } from '../utils/subscriptionService';

interface ActivationModalProps {
  isOpen: boolean;
  onClose: () => void;
  user: any;
  onActivate: (userId: string, planId: SubscriptionPlan) => Promise<void>;
}

const SUBSCRIPTION_PLANS_CONFIG = [
  { id: SubscriptionPlan.MONTHLY, name: '', icon: Zap, color: 'text-green-600', bg: 'bg-green-50', border: 'border-green-200' },
  { id: SubscriptionPlan.QUARTERLY, name: ' ', icon: Calendar, color: 'text-purple-600', bg: 'bg-purple-50', border: 'border-purple-200' },
  { id: SubscriptionPlan.YEARLY, name: '', icon: Gift, color: 'text-blue-600', bg: 'bg-blue-50', border: 'border-blue-200' },
  { id: SubscriptionPlan.LIFETIME, name: ' ', icon: Crown, color: 'text-yellow-600', bg: 'bg-yellow-50', border: 'border-yellow-200' },
  { id: SubscriptionPlan.FIVE_MINUTES, name: '5 ', icon: Clock, color: 'text-orange-600', bg: 'bg-orange-50', border: 'border-orange-200' },
  { id: SubscriptionPlan.ZERO, name: '', icon: Lock, color: 'text-gray-600', bg: 'bg-gray-50', border: 'border-gray-200' },
  { id: SubscriptionPlan.TAHWEELA, name: '', icon: RefreshCw, color: 'text-indigo-600', bg: 'bg-indigo-50', border: 'border-indigo-200' },
];

const PLAN_STYLES: Record<SubscriptionPlan, { from: string; to: string; ring: string }> = {
  [SubscriptionPlan.MONTHLY]: { from: 'from-blue-50', to: 'to-indigo-50', ring: 'ring-blue-300' },
  [SubscriptionPlan.QUARTERLY]: { from: 'from-purple-50', to: 'to-pink-50', ring: 'ring-purple-300' },
  [SubscriptionPlan.YEARLY]: { from: 'from-sky-50', to: 'to-blue-50', ring: 'ring-blue-300' },
  [SubscriptionPlan.LIFETIME]: { from: 'from-yellow-50', to: 'to-amber-50', ring: 'ring-amber-300' },
  [SubscriptionPlan.FIVE_MINUTES]: { from: 'from-orange-50', to: 'to-red-50', ring: 'ring-orange-300' },
  [SubscriptionPlan.ZERO]: { from: 'from-gray-50', to: 'to-slate-100', ring: 'ring-gray-300' },
  [SubscriptionPlan.TAHWEELA]: { from: 'from-indigo-50', to: 'to-violet-50', ring: 'ring-indigo-300' },
};

const getUserDisplayName = (u: any) => {
  const dn = u?.displayName;
  if (typeof dn === 'string' && dn.trim()) return dn.trim();
  if (dn && typeof dn === 'object') {
    const objVal = dn.full_name || dn.name || dn.ar || dn.en;
    if (typeof objVal === 'string' && objVal.trim()) return objVal.trim();
  }
  if (typeof u?.email === 'string' && u.email.includes('@')) {
    return u.email.split('@')[0];
  }
  return '';
};

const ActivationModal: React.FC<ActivationModalProps> = ({ isOpen, onClose, user, onActivate }) => {
  const [selectedPlan, setSelectedPlan] = useState<SubscriptionPlan>(SubscriptionPlan.MONTHLY);
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Reset selection when modal opens/closes or user changes
  React.useEffect(() => {
    if (isOpen) {
      setSelectedPlan(SubscriptionPlan.MONTHLY);
      setIsSubmitting(false);
    }
  }, [isOpen, user]);

  const handleActivate = async () => {
    if (isSubmitting) return;
    setIsSubmitting(true);
    try {
      await onActivate(user.id, selectedPlan);
    } catch (error) {
      console.error('Activation failed', error);
      setIsSubmitting(false);
    }
  };

  if (!isOpen || !user) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4 transition-opacity duration-200">
      <div className="bg-white w-full max-w-2xl rounded-xl shadow-xl transform transition-all flex flex-col max-h-[90vh]">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-gray-100 flex-shrink-0">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-full bg-blue-100 flex items-center justify-center">
              <Zap className="w-5 h-5 text-blue-600" />
            </div>
            <div>
              <h4 className="text-xl font-bold text-gray-800">  </h4>
              <p className="text-sm text-gray-500">: {getUserDisplayName(user)}</p>
            </div>
          </div>
          <button 
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600 transition-colors rounded-lg hover:bg-gray-100 p-1"
          >
            <X className="w-6 h-6" />
          </button>
        </div>
        
        <div className="p-6 overflow-y-auto flex-1">
          <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
            {SUBSCRIPTION_PLANS_CONFIG.map((plan) => {
              const styles = PLAN_STYLES[plan.id];
              const selected = selectedPlan === plan.id;
              return (
                <div
                  key={plan.id}
                  onClick={() => setSelectedPlan(plan.id)}
                  className={`rounded-xl p-4 border shadow-sm bg-gradient-to-br ${styles.from} ${styles.to} cursor-pointer transition-all ${
                    selected ? `ring-2 ${styles.ring}` : 'hover:shadow-md'
                  }`}
                >
                  <div className="flex items-center justify-between mb-2">
                    <div className="w-10 h-10 rounded-full bg-white/70 flex items-center justify-center">
                      <plan.icon className={`w-5 h-5 ${plan.color}`} />
                    </div>
                    {selected && <CheckCircle className={`w-5 h-5 ${plan.color}`} />}
                  </div>
                  <div className="font-bold text-gray-800">{plan.name}</div>
                  {selected && <div className={`text-xs font-medium ${plan.color}`}> </div>}
                </div>
              );
            })}
          </div>
        </div>

        {/* Footer */}
        <div className="flex items-center justify-end gap-3 p-6 bg-gray-50 rounded-b-xl border-t border-gray-100 flex-shrink-0">
          <button
            onClick={onClose}
            disabled={isSubmitting}
            className="px-5 py-2.5 rounded-lg border border-gray-300 bg-white text-gray-700 font-medium hover:bg-gray-50 focus:ring-2 focus:ring-gray-200 transition-all disabled:opacity-50"
          >
            
          </button>
          <button
            onClick={handleActivate}
            disabled={isSubmitting}
            className="px-5 py-2.5 rounded-lg bg-blue-600 text-white font-medium hover:bg-blue-700 shadow-lg shadow-blue-600/20 focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-all flex items-center gap-2 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {isSubmitting ? (
              <div className="w-5 h-5 border-2 border-white/30 border-t-white rounded-full animate-spin" />
            ) : (
              <CheckCircle className="w-5 h-5" />
            )}
            {isSubmitting ? ' ...' : ' '}
          </button>
        </div>
      </div>
    </div>
  );
};

export default ActivationModal;
