import React, { useState, useEffect } from 'react';
import { db } from '@/firebase';
import { collection, query, onSnapshot, orderBy, doc, updateDoc, deleteDoc, Timestamp, where, getDocs, limit } from 'firebase/firestore';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Input } from '@/components/ui/input';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from '@/components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { useToast } from '@/hooks/use-toast';
import { Users, Search, Trash2, Edit, UserCheck, ShieldCheck } from 'lucide-react';
import { SubscriptionPlan, calculateEndDate } from '@/utils/subscriptionService';

interface ResellerRequest {
  id: string;
  uid?: string;
  email: string;
  password?: string;
  status: 'pending' | 'active' | 'inactive';
  packageName?: string;
  ownerId: string;
  createdAt: any;
  startDate?: any;
  endDate?: any;
  deletionRequested?: boolean;
  deletionReason?: string;
  deletionStatus?: 'pending' | 'rejected';
  cancelRequested?: boolean;
  cancelReason?: string;
  cancelStatus?: 'pending' | 'rejected';
}

const PLANS = [
  { value: SubscriptionPlan.MONTHLY, label: ' ' },
  { value: SubscriptionPlan.QUARTERLY, label: '  ' },
  { value: SubscriptionPlan.YEARLY, label: ' ' },
  { value: SubscriptionPlan.LIFETIME, label: '  ' },
];

export const ResellerAdmin = () => {
  const [requests, setRequests] = useState<ResellerRequest[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedRequest, setSelectedRequest] = useState<ResellerRequest | null>(null);
  const [isEditOpen, setIsEditOpen] = useState(false);
  const [editForm, setEditForm] = useState({ status: '', packageName: '' });
  
  // Delete Review States
  const [isDeleteReviewOpen, setIsDeleteReviewOpen] = useState(false);
  const [selectedDeleteRequest, setSelectedDeleteRequest] = useState<ResellerRequest | null>(null);

  // Cancel Package Review States
  const [isCancelReviewOpen, setIsCancelReviewOpen] = useState(false);
  const [selectedCancelRequest, setSelectedCancelRequest] = useState<ResellerRequest | null>(null);

  // Main Reseller Activation States
  const [activeTab, setActiveTab] = useState('requests');
  const [mainResellers, setMainResellers] = useState<any[]>([]);
  const [isActivateDialogOpen, setIsActivateDialogOpen] = useState(false);
  const [activateEmail, setActivateEmail] = useState('');
  const [isActivating, setIsActivating] = useState(false);

  const { toast } = useToast();

  useEffect(() => {
    // 1. Fetch Reseller Requests (Sub-accounts)
    const q = query(collection(db, 'reseller_requests'), orderBy('createdAt', 'desc'));
    const unsubscribe = onSnapshot(q, (snapshot) => {
      const docs = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as ResellerRequest));
      setRequests(docs);
      setLoading(false);
    }, (error) => {
      console.error("Error fetching reseller requests:", error);
      setLoading(false);
    });

    // 2. Fetch Main Resellers (Active Parents)
    const qMain = query(collection(db, 'users'), where('isResellerEnabled', '==', true));
    const unsubscribeMain = onSnapshot(qMain, (snapshot) => {
      const docs = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setMainResellers(docs);
    });

    return () => {
      unsubscribe();
      unsubscribeMain();
    };
  }, []);

  const handleActivateReseller = async () => {
    if (!activateEmail.trim()) {
      toast({ title: '', description: '   ', variant: 'destructive' });
      return;
    }

    setIsActivating(true);
    try {
      // Find user by email
      const usersRef = collection(db, 'users');
      const q = query(usersRef, where('email', '==', activateEmail.trim()), limit(1));
      const querySnapshot = await getDocs(q);

      if (querySnapshot.empty) {
        toast({ title: '', description: '       ', variant: 'destructive' });
        setIsActivating(false);
        return;
      }

      const userDoc = querySnapshot.docs[0];
      
      // Update user to enable reseller system
      await updateDoc(doc(db, 'users', userDoc.id), {
        isResellerEnabled: true
      });

      toast({ title: ' ', description: '      ' });
      setIsActivateDialogOpen(false);
      setActivateEmail('');
    } catch (error) {
      console.error("Error activating reseller:", error);
      toast({ title: '', description: '   ', variant: 'destructive' });
    } finally {
      setIsActivating(false);
    }
  };

  const handleDeactivateReseller = async (userId: string) => {
    if (!window.confirm('        ')) return;
    try {
      await updateDoc(doc(db, 'users', userId), {
        isResellerEnabled: false
      });
      toast({ title: ' ', description: '    ' });
    } catch (error) {
      console.error("Error deactivating reseller:", error);
      toast({ title: '', description: '   ', variant: 'destructive' });
    }
  };

  const handleEditClick = (req: ResellerRequest) => {
    setSelectedRequest(req);
    setEditForm({ 
      status: req.status, 
      packageName: req.packageName || '' 
    });
    setIsEditOpen(true);
  };

  const handleSave = async () => {
    if (!selectedRequest) return;
    try {
      const updateData: any = {
        status: editForm.status,
        packageName: editForm.packageName
      };

      let subscriptionData: any = null;
      let isActive = false;

      //          
      if (editForm.status === 'active' && editForm.packageName) {
        const startDate = new Date();
        const endDate = calculateEndDate(editForm.packageName as SubscriptionPlan, startDate);
        updateData.startDate = startDate;
        updateData.endDate = endDate;
        
        isActive = true;
        subscriptionData = {
          planType: editForm.packageName,
          startDate: startDate,
          endDate: endDate,
          isActive: true,
          activatedAt: startDate
        };
      } else if (editForm.status !== 'active') {
        isActive = false;
        subscriptionData = {
          isActive: false
        };
      }

      //    
      await updateDoc(doc(db, 'reseller_requests', selectedRequest.id), updateData);

      //    
      if (selectedRequest.uid) {
        const userUpdateData: any = {
          isActive: isActive,
          updatedAt: new Date()
        };
        
        if (subscriptionData) {
          userUpdateData.subscription = subscriptionData;
        }

        await updateDoc(doc(db, 'users', selectedRequest.uid), userUpdateData);
      }

      toast({ title: ' ', description: '      ' });
      setIsEditOpen(false);
    } catch (error) {
      console.error("Error updating request:", error);
      toast({ title: '', description: '   ', variant: 'destructive' });
    }
  };

  const handleDelete = async (id: string) => {
    if (!window.confirm('      ')) return;
    try {
      await deleteDoc(doc(db, 'reseller_requests', id));
      toast({ title: ' ', description: '   ' });
    } catch (error) {
      toast({ title: '', description: '   ', variant: 'destructive' });
    }
  };

  const handleReviewDeleteClick = (req: ResellerRequest) => {
    setSelectedDeleteRequest(req);
    setIsDeleteReviewOpen(true);
  };

  const handleAcceptDelete = async () => {
    if (!selectedDeleteRequest) return;
    try {
      // Delete the reseller request
      await deleteDoc(doc(db, 'reseller_requests', selectedDeleteRequest.id));
      
      // Optionally disable the user if uid exists
      if (selectedDeleteRequest.uid) {
        // We might want to keep the user doc but mark it inactive or deleted, 
        // but user asked for deletion from reseller list.
        // Let's just update the user to inactive for safety.
        await updateDoc(doc(db, 'users', selectedDeleteRequest.uid), {
          isActive: false,
          role: 'user' // Downgrade from merchant/reseller
        });
      }

      toast({ title: ' ', description: '       ' });
      setIsDeleteReviewOpen(false);
    } catch (error) {
      console.error("Error accepting delete:", error);
      toast({ title: '', description: '    ', variant: 'destructive' });
    }
  };

  const handleRejectDelete = async () => {
    if (!selectedDeleteRequest) return;
    try {
      await updateDoc(doc(db, 'reseller_requests', selectedDeleteRequest.id), {
        deletionRequested: false,
        deletionStatus: 'rejected'
      });
      toast({ title: ' ', description: '     ' });
      setIsDeleteReviewOpen(false);
    } catch (error) {
      console.error("Error rejecting delete:", error);
      toast({ title: '', description: '    ', variant: 'destructive' });
    }
  };

  const handleReviewCancelClick = (req: ResellerRequest) => {
    setSelectedCancelRequest(req);
    setIsCancelReviewOpen(true);
  };

  const handleAcceptCancel = async () => {
    if (!selectedCancelRequest) return;
    try {
      // 1. Update reseller request status to inactive and clear package details
      await updateDoc(doc(db, 'reseller_requests', selectedCancelRequest.id), {
        status: 'inactive',
        packageName: null,
        startDate: null,
        endDate: null,
        cancelRequested: false,
        cancelStatus: null
      });

      // 2. Update user document to inactive
      if (selectedCancelRequest.uid) {
        await updateDoc(doc(db, 'users', selectedCancelRequest.uid), {
          isActive: false,
          'subscription.isActive': false,
          'subscription.planId': null,
          'subscription.endDate': null
        });
      }

      toast({ title: ' ', description: '       ' });
      setIsCancelReviewOpen(false);
    } catch (error) {
      console.error("Error accepting cancel:", error);
      toast({ title: '', description: '    ', variant: 'destructive' });
    }
  };

  const handleRejectCancel = async () => {
    if (!selectedCancelRequest) return;
    try {
      await updateDoc(doc(db, 'reseller_requests', selectedCancelRequest.id), {
        cancelRequested: false,
        cancelStatus: 'rejected'
      });
      toast({ title: ' ', description: '      ' });
      setIsCancelReviewOpen(false);
    } catch (error) {
      console.error("Error rejecting cancel:", error);
      toast({ title: '', description: '    ', variant: 'destructive' });
    }
  };

  const getPlanLabel = (value?: string) => {
    return PLANS.find(p => p.value === value)?.label || value || '-';
  };

  const filteredRequests = requests.filter(req => 
    req.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
    req.packageName?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="space-y-6">
      <Tabs defaultValue="requests" value={activeTab} onValueChange={setActiveTab} className="w-full">
        <TabsList className="grid w-full grid-cols-2 mb-4">
          <TabsTrigger value="requests">  </TabsTrigger>
          <TabsTrigger value="main_resellers">   ()</TabsTrigger>
        </TabsList>

        <TabsContent value="requests">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between">
              <CardTitle>   </CardTitle>
              <div className="relative w-64">
                <Search className="absolute right-2 top-2.5 h-4 w-4 text-gray-500" />
                <Input
                  placeholder="  ..."
                  className="pr-8"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
              </div>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead className="text-right"> </TableHead>
                    <TableHead className="text-right"> </TableHead>
                    <TableHead className="text-right"> </TableHead>
                    <TableHead className="text-right"></TableHead>
                    <TableHead className="text-right"></TableHead>
                    <TableHead className="text-right"></TableHead>
                    <TableHead className="text-right"></TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {loading ? (
                    <TableRow>
                      <TableCell colSpan={7} className="text-center"> ...</TableCell>
                    </TableRow>
                  ) : filteredRequests.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={7} className="text-center">  </TableCell>
                    </TableRow>
                  ) : (
                    filteredRequests.map((req) => (
                      <TableRow key={req.id}>
                        <TableCell>
                          <div className="flex items-center gap-2">
                            {req.email}
                            {req.deletionRequested && (
                              <Badge 
                                variant="destructive" 
                                className="cursor-pointer hover:bg-red-700 animate-pulse"
                                onClick={() => handleReviewDeleteClick(req)}
                              >
                                 
                              </Badge>
                            )}
                            {req.cancelRequested && (
                              <Badge 
                                className="bg-orange-500 hover:bg-orange-600 cursor-pointer animate-pulse"
                                onClick={() => handleReviewCancelClick(req)}
                              >
                                  
                              </Badge>
                            )}
                          </div>
                        </TableCell>
                        <TableCell className="font-mono text-xs">{req.password}</TableCell>
                        <TableCell>{req.phone || '-'}</TableCell>
                        <TableCell>
                          {req.status === 'active' ? (
                            <Badge className="bg-green-100 text-green-800 hover:bg-green-100"></Badge>
                          ) : req.status === 'pending' ? (
                            <Badge className="bg-yellow-100 text-yellow-800 hover:bg-yellow-100"> </Badge>
                          ) : (
                            <Badge className="bg-red-100 text-red-800 hover:bg-red-100"> </Badge>
                          )}
                        </TableCell>
                        <TableCell>{getPlanLabel(req.packageName)}</TableCell>
                        <TableCell>
                          <div className="flex flex-col gap-1">
                            <span className="text-xs text-gray-500">
                              : {req.createdAt?.seconds ? new Date(req.createdAt.seconds * 1000).toLocaleDateString('ar-SA') : '-'}
                            </span>
                            {req.status === 'active' && req.endDate && (
                              <span className="text-xs text-green-600 font-medium">
                                : {req.endDate.seconds ? new Date(req.endDate.seconds * 1000).toLocaleDateString('ar-SA') : '-'}
                              </span>
                            )}
                          </div>
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center gap-2">
                            <Button variant="outline" size="sm" onClick={() => handleEditClick(req)}>
                              <Edit className="h-4 w-4" />
                            </Button>
                            <Button variant="ghost" size="sm" className="text-red-600" onClick={() => handleDelete(req.id)}>
                              <Trash2 className="h-4 w-4" />
                            </Button>
                          </div>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="main_resellers">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between">
              <CardTitle className="flex items-center gap-2">
                <ShieldCheck className="h-5 w-5 text-blue-600" />
                   
              </CardTitle>
              <Dialog open={isActivateDialogOpen} onOpenChange={setIsActivateDialogOpen}>
                <DialogContent>
                  <DialogHeader>
                    <DialogTitle>   </DialogTitle>
                  </DialogHeader>
                  <div className="space-y-4 py-4">
                    <div className="space-y-2">
                      <label className="text-sm font-medium">   </label>
                      <Input 
                        placeholder="email@example.com" 
                        value={activateEmail}
                        onChange={(e) => setActivateEmail(e.target.value)}
                      />
                    </div>
                    <Button 
                      className="w-full bg-blue-600 hover:bg-blue-700" 
                      onClick={handleActivateReseller}
                      disabled={isActivating}
                    >
                      {isActivating ? ' ...' : ' '}
                    </Button>
                  </div>
                </DialogContent>
                <Button onClick={() => setIsActivateDialogOpen(true)} className="bg-blue-600 hover:bg-blue-700">
                  <UserCheck className="mr-2 h-4 w-4" />
                    
                </Button>
              </Dialog>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead className="text-right"> </TableHead>
                    <TableHead className="text-right"></TableHead>
                    <TableHead className="text-right"> </TableHead>
                    <TableHead className="text-right"></TableHead>
                    <TableHead className="text-right"></TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {mainResellers.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={5} className="text-center py-8 text-gray-500">
                             
                      </TableCell>
                    </TableRow>
                  ) : (
                    mainResellers.map((user) => (
                      <TableRow key={user.id}>
                        <TableCell className="font-medium">{user.email}</TableCell>
                        <TableCell>{user.displayName || '-'}</TableCell>
                        <TableCell>
                          {user.createdAt?.seconds ? new Date(user.createdAt.seconds * 1000).toLocaleDateString('ar-SA') : '-'}
                        </TableCell>
                        <TableCell>
                          <Badge className="bg-blue-100 text-blue-800 hover:bg-blue-100 flex w-fit items-center gap-1">
                            <ShieldCheck className="h-3 w-3" />
                             
                          </Badge>
                        </TableCell>
                        <TableCell>
                          <Button 
                            variant="ghost" 
                            size="sm" 
                            className="text-red-600 hover:text-red-700 hover:bg-red-50"
                            onClick={() => handleDeactivateReseller(user.id)}
                          >
                            <Trash2 className="h-4 w-4 mr-1" />
                            
                          </Button>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      <Dialog open={isEditOpen} onOpenChange={setIsEditOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>  </DialogTitle>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <label className="text-sm font-medium"></label>
              <Select value={editForm.status} onValueChange={(val) => setEditForm(prev => ({ ...prev, status: val }))}>
                <SelectTrigger>
                  <SelectValue placeholder=" " />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="pending"> </SelectItem>
                  <SelectItem value="active"></SelectItem>
                  <SelectItem value="inactive"> </SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <label className="text-sm font-medium"> </label>
              <Select value={editForm.packageName} onValueChange={(val) => setEditForm(prev => ({ ...prev, packageName: val }))}>
                <SelectTrigger>
                  <SelectValue placeholder=" " />
                </SelectTrigger>
                <SelectContent>
                  {PLANS.map((plan) => (
                    <SelectItem key={plan.value} value={plan.value}>{plan.label}</SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={() => setIsEditOpen(false)}></Button>
            <Button onClick={handleSave} className="bg-blue-600 hover:bg-blue-700 text-white"> </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      <Dialog open={isDeleteReviewOpen} onOpenChange={setIsDeleteReviewOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle className="text-red-600">   </DialogTitle>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <div className="bg-red-50 p-4 rounded-lg border border-red-100">
              <h4 className="font-semibold text-red-800 mb-2">    :</h4>
              <p className="text-gray-700 whitespace-pre-wrap">
                {selectedDeleteRequest?.deletionReason || '   '}
              </p>
            </div>
            <div className="text-sm text-gray-500">
              <p> : {selectedDeleteRequest?.email}</p>
              <p> : {new Date().toLocaleDateString('ar-SA')}</p>
            </div>
            <div className="bg-yellow-50 p-3 rounded text-sm text-yellow-800 border border-yellow-200">
              :               .
            </div>
          </div>
          <DialogFooter className="gap-2 sm:gap-0">
            <Button 
              variant="outline" 
              onClick={handleRejectDelete}
              className="text-red-600 border-red-200 hover:bg-red-50"
            >
               
            </Button>
            <Button 
              onClick={handleAcceptDelete} 
              className="bg-red-600 hover:bg-red-700 text-white"
            >
               
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      <Dialog open={isCancelReviewOpen} onOpenChange={setIsCancelReviewOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle className="text-orange-600">   </DialogTitle>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <div className="bg-orange-50 p-4 rounded-lg border border-orange-100">
              <h4 className="font-semibold text-orange-800 mb-2">    :</h4>
              <p className="text-gray-700 whitespace-pre-wrap">
                {selectedCancelRequest?.cancelReason || '   '}
              </p>
            </div>
            <div className="text-sm text-gray-500">
              <p> : {selectedCancelRequest?.email}</p>
              <p> : {new Date().toLocaleDateString('ar-SA')}</p>
            </div>
            <div className="bg-yellow-50 p-3 rounded text-sm text-yellow-800 border border-yellow-200">
              :         .
            </div>
          </div>
          <DialogFooter className="gap-2 sm:gap-0">
            <Button 
              variant="outline" 
              onClick={handleRejectCancel}
              className="text-red-600 border-red-200 hover:bg-red-50"
            >
               
            </Button>
            <Button 
              onClick={handleAcceptCancel} 
              className="bg-orange-600 hover:bg-orange-700 text-white"
            >
               
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  );
};
