import React, { useState } from 'react';
import { Card, CardContent } from './ui/card';
import { Badge } from './ui/badge';
import { Button } from './ui/button';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';
import { User, Calendar, Settings, Crown, Clock, Infinity, Gift, Zap, Mail, Lock, Phone, ArrowUpDown } from 'lucide-react'
import { UserWithSubscription, SubscriptionPlan, parseFirestoreDate, getPlanDisplayName } from '../utils/subscriptionService';
import { useIsMobile } from '@/hooks/use-mobile';

interface UserCardProps {
  user: UserWithSubscription;
  onActivateSubscription?: (userId: string, planType: SubscriptionPlan) => void;
  onRequestActivation?: (user: UserWithSubscription) => void;
  onDeactivateSubscription?: (userId: string) => void;
  onToggleUserStatus?: (userId: string) => void;
  onDeleteUser?: (userId: string) => void;
}

const UserCard: React.FC<UserCardProps> = ({
  user,
  onActivateSubscription,
  onRequestActivation,
  onDeactivateSubscription,
  onDeleteUser
}) => {
  const [selectedPlan, setSelectedPlan] = useState<SubscriptionPlan>(SubscriptionPlan.MONTHLY);
  const [showPlanSelector, setShowPlanSelector] = useState(false);
  const isMobile = useIsMobile();
  // Helper: safely get display name
  const getUserDisplayName = (u: any) => {
    const dn = u?.displayName;
    if (typeof dn === 'string' && dn.trim()) return dn.trim();
    if (dn && typeof dn === 'object') {
      const objVal = dn.full_name || dn.name || dn.ar || dn.en;
      if (typeof objVal === 'string' && objVal.trim()) return objVal.trim();
    }
    if (typeof u?.email === 'string' && u.email.includes('@')) {
      return u.email.split('@')[0];
    }
    return ' ';
  };

  const getPlanIcon = (planType: SubscriptionPlan) => {
    switch (planType) {
      case SubscriptionPlan.LIFETIME:
        return <Crown className="w-4 h-4 text-yellow-500" />
      case SubscriptionPlan.YEARLY:
        return <Gift className="w-4 h-4 text-blue-500" />
      case SubscriptionPlan.QUARTERLY:
        return <Calendar className="w-4 h-4 text-purple-500" />
      case SubscriptionPlan.MONTHLY:
        return <Zap className="w-4 h-4 text-green-500" />
      case SubscriptionPlan.FIVE_MINUTES:
        return <Clock className="w-4 h-4 text-orange-500" />
      case SubscriptionPlan.ZERO:
        return <Lock className="w-4 h-4 text-gray-500" />
      case SubscriptionPlan.TAHWEELA:
        return <ArrowUpDown className="w-4 h-4 text-blue-600" />
      case SubscriptionPlan.BRANCH:
        return <User className="w-4 h-4 text-indigo-500" />
      default:
        return null
    }
  };

  const formatDate = (date: any) => {
    if (!date) return ' ';
    const dateObj = parseFirestoreDate(date);
    
    // Validate date
    if (!dateObj) {
      return '  ';
    }
    
    return dateObj.toLocaleDateString('ar-EG');
  };

  return (
    <Card className="hover:shadow-lg transition-shadow">
      <CardContent className="p-6">
        <div className="flex items-start justify-between mb-4">
          <div className="flex items-center space-x-3 space-x-reverse">
            <div className="w-10 h-10 bg-blue-100 rounded-full flex items-center justify-center">
              <User className="w-5 h-5 text-blue-600" />
            </div>
            <div>
              <h3 className="font-semibold text-gray-900">
                {getUserDisplayName(user)}
              </h3>
              <div className="flex items-center text-sm text-gray-500">
                <Mail className="w-3 h-3 ml-1" />
                {user.email}
              </div>
              <div className="flex items-center text-sm text-gray-500 mt-1">
                <Phone className="w-3 h-3 ml-1" />
                {user.phone || ' '}
              </div>
            </div>
          </div>
          <div className={`px-2 py-0.5 rounded-[8px] text-xs font-medium border ${
            user.isActive 
              ? 'bg-green-50 text-green-700 border-green-200' 
              : 'bg-secondary/10 text-secondary border-secondary/20'
          }`}>
            {user.isActive ? '' : ' '}
          </div>
        </div>

        {user.subscription && (
          <div className="mb-4 p-3 bg-gray-50 rounded-lg">
            <div className="flex items-center justify-between mb-2">
              <div className="flex items-center space-x-2 space-x-reverse">
                {getPlanIcon(user.subscription.planType)}
                <span className="text-sm font-medium">
                  {getPlanDisplayName(user.subscription.planType)}
                </span>
              </div>
              <Badge variant={user.subscription.isActive ? "default" : "destructive"}>
                {user.subscription.isActive ? '' : ''}
              </Badge>
            </div>
            <div className="text-xs text-gray-600">
              <div className="flex items-center mb-1">
                <Calendar className="w-3 h-3 ml-1" />
                : {formatDate(user.subscription.startDate)}
              </div>
              {user.subscription.endDate && (
                <div className="flex items-center">
                  <Calendar className="w-3 h-3 ml-1" />
                  : {formatDate(user.subscription.endDate)}
                </div>
              )}
            </div>
          </div>
        )}

        <div className="space-y-3">
          {/*    */}
          {onActivateSubscription && showPlanSelector && (
            <div className="p-3 bg-blue-50 rounded-lg border border-blue-200">
              <div className="mb-2">
                <label className="text-sm font-medium text-gray-700 mb-2 block">
                    :
                </label>
                {isMobile ? (
                  <select
                    className="w-full h-10 border border-input bg-background rounded-md px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-ring"
                    value={selectedPlan}
                    onChange={(e) => setSelectedPlan(e.target.value as SubscriptionPlan)}
                  >
                    <option value={SubscriptionPlan.MONTHLY}></option>
                    <option value={SubscriptionPlan.QUARTERLY}> </option>
                    <option value={SubscriptionPlan.YEARLY}></option>
                    <option value={SubscriptionPlan.LIFETIME}> </option>
                    <option value={SubscriptionPlan.FIVE_MINUTES}>5 </option>
                    <option value={SubscriptionPlan.ZERO}></option>
                    <option value={SubscriptionPlan.TAHWEELA}></option>
                  </select>
                ) : (
                  <Select value={selectedPlan} onValueChange={(value) => setSelectedPlan(value as SubscriptionPlan)}>
                    <SelectTrigger className="w-full">
                      <SelectValue placeholder="  " />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value={SubscriptionPlan.MONTHLY}>
                        <div className="flex items-center space-x-2 space-x-reverse">
                          <Clock className="w-4 h-4" />
                          <span></span>
                        </div>
                      </SelectItem>
                      <SelectItem value={SubscriptionPlan.QUARTERLY}>
                        <div className="flex items-center space-x-2 space-x-reverse">
                          <Calendar className="w-4 h-4" />
                          <span> </span>
                        </div>
                      </SelectItem>
                      <SelectItem value={SubscriptionPlan.YEARLY}>
                        <div className="flex items-center space-x-2 space-x-reverse">
                          <Calendar className="w-4 h-4" />
                          <span></span>
                        </div>
                      </SelectItem>
                      <SelectItem value={SubscriptionPlan.LIFETIME}>
                        <div className="flex items-center space-x-2 space-x-reverse">
                          <Infinity className="w-4 h-4" />
                          <span> </span>
                        </div>
                      </SelectItem>
                      <SelectItem value={SubscriptionPlan.FIVE_MINUTES}>
                        <div className="flex items-center space-x-2 space-x-reverse">
                          <Clock className="w-4 h-4" />
                          <span>5 </span>
                        </div>
                      </SelectItem>
                      <SelectItem value={SubscriptionPlan.ZERO}>
                        <div className="flex items-center space-x-2 space-x-reverse">
                          <Lock className="w-4 h-4" />
                          <span></span>
                        </div>
                      </SelectItem>
                      <SelectItem value={SubscriptionPlan.TAHWEELA}>
                        <div className="flex items-center space-x-2 space-x-reverse">
                          <ArrowUpDown className="w-4 h-4" />
                          <span></span>
                        </div>
                      </SelectItem>
                    </SelectContent>
                  </Select>
                )}
              </div>
              <div className="flex space-x-2 space-x-reverse">
                <Button
                  size="sm"
                  onClick={() => {
                    onActivateSubscription(user.id, selectedPlan);
                    setShowPlanSelector(false);
                  }}
                  className="flex-1 bg-blue-600 hover:bg-blue-700 text-white"
                >
                   
                </Button>
                <Button
                  size="sm"
                  variant="outline"
                  onClick={() => setShowPlanSelector(false)}
                  className="flex-1"
                >
                  
                </Button>
              </div>
            </div>
          )}

          {/*   */}
          <div className="flex space-x-2 space-x-reverse">
            {(onActivateSubscription || onRequestActivation) && !showPlanSelector && (
              <Button
                size="sm"
                variant="outline"
                onClick={() => onRequestActivation ? onRequestActivation(user) : setShowPlanSelector(true)}
                className="flex-1"
              >
                 
              </Button>
            )}
            {onDeactivateSubscription && (
              <Button
                size="sm"
                variant="outline"
                onClick={() => onDeactivateSubscription(user.id)}
                className="flex-1"
              >
                 
              </Button>
            )}
            {onDeleteUser && (
              <Button
                size="sm"
                variant="destructive"
                onClick={() => onDeleteUser(user.id)}
              >
                <Settings className="w-4 h-4" />
              </Button>
            )}
          </div>
        </div>
      </CardContent>
    </Card>
  );
};

export default React.memo(UserCard);
