import React, { useEffect, useState } from 'react';
import { X, AlertTriangle, Clock, Bell, Volume2, VolumeX } from 'lucide-react';
import { NotificationItem, NotificationType } from '../utils/notificationService';

interface NotificationAlertProps {
  notification: NotificationItem | null;
  onClose: () => void;
  soundEnabled: boolean;
  onToggleSound: () => void;
}

const NotificationAlert: React.FC<NotificationAlertProps> = ({
  notification,
  onClose,
  soundEnabled,
  onToggleSound
}) => {
  const [isVisible, setIsVisible] = useState(false);
  const [audioContext, setAudioContext] = useState<AudioContext | null>(null);

  useEffect(() => {
    if (notification) {
      setIsVisible(true);
      if (soundEnabled) {
        playNotificationSound(notification.type);
      }
      
      // Auto-hide after 10 seconds for non-urgent notifications
      if (notification.type !== 'urgent') {
        const timer = setTimeout(() => {
          handleClose();
        }, 10000);
        
        return () => clearTimeout(timer);
      }
    }
  }, [notification, soundEnabled]);

  useEffect(() => {
    // Initialize audio context
    if (typeof window !== 'undefined' && window.AudioContext) {
      setAudioContext(new AudioContext());
    }
  }, []);

  const playNotificationSound = (type: NotificationType) => {
    if (!audioContext || !soundEnabled) return;

    try {
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);
      
      // Different frequencies for different notification types
      switch (type) {
        case 'urgent':
          // High-pitched urgent sound
          oscillator.frequency.setValueAtTime(800, audioContext.currentTime);
          oscillator.frequency.setValueAtTime(1000, audioContext.currentTime + 0.1);
          oscillator.frequency.setValueAtTime(800, audioContext.currentTime + 0.2);
          gainNode.gain.setValueAtTime(0.3, audioContext.currentTime);
          gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.5);
          oscillator.start(audioContext.currentTime);
          oscillator.stop(audioContext.currentTime + 0.5);
          break;
          
        case 'warning':
          // Medium-pitched warning sound
          oscillator.frequency.setValueAtTime(600, audioContext.currentTime);
          oscillator.frequency.setValueAtTime(700, audioContext.currentTime + 0.15);
          gainNode.gain.setValueAtTime(0.2, audioContext.currentTime);
          gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.3);
          oscillator.start(audioContext.currentTime);
          oscillator.stop(audioContext.currentTime + 0.3);
          break;
          
        case 'info':
          // Gentle info sound
          oscillator.frequency.setValueAtTime(400, audioContext.currentTime);
          gainNode.gain.setValueAtTime(0.1, audioContext.currentTime);
          gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.2);
          oscillator.start(audioContext.currentTime);
          oscillator.stop(audioContext.currentTime + 0.2);
          break;
      }
    } catch (error) {
      console.error('   :', error);
    }
  };

  const handleClose = () => {
    setIsVisible(false);
    setTimeout(() => {
      onClose();
    }, 300); // Wait for animation to complete
  };

  const getAlertStyles = (type: NotificationType) => {
    switch (type) {
      case 'urgent':
        return {
          bg: 'bg-red-500',
          border: 'border-red-600',
          text: 'text-white',
          icon: <AlertTriangle className="w-6 h-6" />,
          pulse: 'animate-pulse'
        };
      case 'warning':
        return {
          bg: 'bg-yellow-500',
          border: 'border-yellow-600',
          text: 'text-white',
          icon: <Clock className="w-6 h-6" />,
          pulse: ''
        };
      case 'info':
        return {
          bg: 'bg-blue-500',
          border: 'border-blue-600',
          text: 'text-white',
          icon: <Bell className="w-6 h-6" />,
          pulse: ''
        };
      default:
        return {
          bg: 'bg-gray-500',
          border: 'border-gray-600',
          text: 'text-white',
          icon: <Bell className="w-6 h-6" />,
          pulse: ''
        };
    }
  };

  const formatDate = (date: Date) => {
    return new Intl.DateTimeFormat('ar-SA', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    }).format(date);
  };

  if (!notification) return null;

  const styles = getAlertStyles(notification.type);

  return (
    <div className="fixed top-4 right-4 z-[9999]">
      <div
        className={`
          transform transition-all duration-300 ease-in-out
          ${isVisible ? 'translate-x-0 opacity-100' : 'translate-x-full opacity-0'}
          ${styles.bg} ${styles.border} ${styles.text} ${styles.pulse}
          border-2 rounded-lg shadow-2xl max-w-md min-w-80
          backdrop-blur-sm
        `}
      >
        {/* Header */}
        <div className="flex items-center justify-between p-4 border-b border-white border-opacity-20">
          <div className="flex items-center gap-3">
            {styles.icon}
            <h3 className="font-bold text-lg">
              {notification.type === 'urgent' && ' !'}
              {notification.type === 'warning' && ''}
              {notification.type === 'info' && ''}
            </h3>
          </div>
          
          <div className="flex items-center gap-2">
            <button
              onClick={onToggleSound}
              className="p-1 hover:bg-white hover:bg-opacity-20 rounded transition-colors"
              title={soundEnabled ? ' ' : ' '}
            >
              {soundEnabled ? (
                <Volume2 className="w-4 h-4" />
              ) : (
                <VolumeX className="w-4 h-4" />
              )}
            </button>
            
            <button
              onClick={handleClose}
              className="p-1 hover:bg-white hover:bg-opacity-20 rounded transition-colors"
            >
              <X className="w-4 h-4" />
            </button>
          </div>
        </div>

        {/* Content */}
        <div className="p-4">
          <p className="text-lg font-medium mb-3">
            {notification.message}
          </p>
          
          <div className="space-y-2 text-sm opacity-90">
            <div>
              <strong>:</strong> {notification.userName}
            </div>
            <div>
              <strong>  :</strong> {formatDate(notification.expiryDate)}
            </div>
            <div>
              <strong> :</strong> {notification.daysRemaining} 
            </div>
          </div>
        </div>

        {/* Progress bar for urgent notifications */}
        {notification.type === 'urgent' && (
          <div className="h-1 bg-white bg-opacity-20">
            <div 
              className="h-full bg-white animate-pulse"
              style={{
                animation: 'progress 10s linear forwards'
              }}
            />
          </div>
        )}
      </div>

      <style jsx>{`
        @keyframes progress {
          from { width: 100%; }
          to { width: 0%; }
        }
      `}</style>
    </div>
  );
};

export default NotificationAlert;